import gsap from 'gsap'


export default class text {
  constructor() {


    const content = document.querySelector('.page');
    const sectionNodes =[...document.querySelectorAll('.section')];
    const [item0, item1, item2, item3, item4, item5, item6, item7] = [...document.querySelectorAll('.wrap')];

    const t0 = gsap.timeline({ paused: true });
    const t1 = gsap.timeline({ paused: true });
    const t2 = gsap.timeline({ paused: true });
    const t3 = gsap.timeline({ paused: true });
    const t4 = gsap.timeline({ paused: true });
    const t5 = gsap.timeline({ paused: true });
    const t6 = gsap.timeline({ paused: true });
    const t7 = gsap.timeline({ paused: true });


    t0.fromTo(
      item0,
      {
          y: '100%',
          opacity: 0,
      },
      {
          y: '0%',
          opacity: 1,
          delay: 0.5,
          duration: 1.7,
          ease: "slow(0.7, 0.7, false)"
          }
    );

    t1.fromTo(
      item1,
      {
          y: '100%',
          opacity: 0,
      },
      {
          y: '0%',
          opacity: 1,
          delay: 0.5,
          duration: 1.7,
          ease: "slow(0.7, 0.7, false)"
          }
    );

    t2.fromTo(
      item2,
      {
          y: '100%',
          opacity: 0,
      },
      {
          y: '0%',
          opacity: 1,
          delay: 0.5,
          duration: 1.7,
          ease: "slow(0.7, 0.7, false)"
          }
    );

    t3.fromTo(
      item3,
      {
          y: '100%',
          opacity: 0,
      },
      {
          y: '0%',
          opacity: 1,
          delay: 0.5,
          duration: 1.7,
          ease: "slow(0.7, 0.7, false)"
          }
    );

    t4.fromTo(
      item4,
      {
          y: '100%',
          opacity: 0,
      },
      {
          y: '0%',
          opacity: 1,
          delay: 0.5,
          duration: 1.7,
          ease: "slow(0.7, 0.7, false)"
          }
    );

    t5.fromTo(
      item5,
      {
          y: '100%',
          opacity: 0,
      },
      {
          y: '0%',
          opacity: 1,
          delay: 0.5,
          duration: 1.7,
          ease: "slow(0.7, 0.7, false)"
          }
    );

    t6.fromTo(
      item6,
      {
          y: '100%',
          opacity: 0,
      },
      {
          y: '0%',
          opacity: 1,
          delay: 0.5,
          duration: 1.7,
          ease: "slow(0.7, 0.7, false)"
          }
    );

    t7.fromTo(
      item7,
      {
          y: '100%',
          opacity: 0,
      },
      {
          y: '0%',
          opacity: 1,
          delay: 0.5,
          duration: 1.7,
          ease: "slow(0.7, 0.7, false)"
          }
    );


    const timelines = [t0, t1, t2, t3, t4, t5, t6, t7, ];

    const INTERSECTION_OPTIONS ={
      threshold: 0.3,
      marginRoot: '0% 0% 0% 0%'
    };

    const callback = (entries) => {
      entries.forEach((entry) => {
        const index = sectionNodes.indexOf(entry.target);

        if (entry.isIntersecting) {
          content.className = "page";

          // start animations
          timelines[index].restart();
          } else {
            timelines[index].reverse();
        }
      });
    };

    const observer = new IntersectionObserver(callback, INTERSECTION_OPTIONS);
    const sections = [...document.querySelectorAll('.section')];
    sections.forEach(section => observer.observe(section));


  }
}
      